import React, { Component } from "react";
import { minutesFromMidnight } from "../utlis";

export class Day extends Component {
  constructor(props) {
    super(props);

    this.state = {
      indexToHighlight: -1,
      interval: null
    };
  }
  static getDerivedStateFromProps(props) {
    if (!areThereStillSomeLessons(props.lessons, props.day)) {
      return {
        indexToHighlight: -1
      };
    }
    const indexToHighlight = findIndexOfLessonToHighlight(props.lessons);

    return ({ indexToHighlight });
  }
  componentDidMount() {
    if (!areThereStillSomeLessons(this.props.lessons, this.props.day)) {
      return;
    }
    const interval = setInterval(() => {
      this.setState((prevState, prevProps) => ({
        indexToHighlight: findIndexOfLessonToHighlight(prevProps.lessons)
      }));
    }, 60 * 1000);
    const indexToHighlight = findIndexOfLessonToHighlight(this.props.lessons);

    this.setState(() => ({ interval, indexToHighlight }));
  }
  componentWillUnmount() {
    if (this.state.interval !== null) {
      clearInterval(this.state.interval);
    }
  }

  renderTime = time => {
    return (
      <React.Fragment>
        {Math.floor(time / 60)
          .toString()
          .padStart(2, "0")}
        <span className="minutes">
          {(time % 60).toString().padStart(2, "0")}
        </span>
      </React.Fragment>
    );
  };

  render() {
    return (
      <div className="lessons">
        {this.props.lessons.map((lesson, index) => (
          <div
            key={lesson.start}
            className={
              "lesson " +
              (index === this.state.indexToHighlight ? "lesson--active" : "")
            }
          >
            {this.renderTime(lesson.start)} - {this.renderTime(lesson.end)}{" "}
            {lesson.lesson} <span className="class">{lesson.class}</span>
          </div>
        ))}
      </div>
    );
  }
}

const areThereStillSomeLessons = (lessons, day) => {
  const now = new Date();
  if (day !== (now.getDay() + 7) % 8) {
    return false;
  }
  const nowMinutes = minutesFromMidnight(now);
  return nowMinutes <= lessons[lessons.length - 1].end;
};

const findIndexOfLessonToHighlight = lessons => {
  return lessons.findIndex(lesson => minutesFromMidnight() <= lesson.end);
};



