import React, { Component } from "react";
import * as R from "ramda";

import plan from "./data/plan";
import hours from "./data/hours";
import { Header } from "./components/Header";
import { Settings } from "./components/Settings";
import { Day } from "./components/Day";

const storageLocation = "settings";
const storage = {
  setData: state =>
    window.localStorage.setItem(
      storageLocation,
      JSON.stringify(state.userSettings)
    ),
  getData: () => JSON.parse(window.localStorage.getItem(storageLocation))
};

const getWeek = () => {
  const now = new Date();
  const onejan = new Date(now.getFullYear(), 0, 1);
  return Math.ceil(((now - onejan) / 86400000 + onejan.getDay() + 1) / 7);
};

const filterAndMapPlan = (plan, settings) => {
  const currentWeek = getWeek() % 2 === 1 ? "odd" : "even";
  const filteredPlan = plan.map(day =>
    day
      .filter(lesson => {
        // mona by wrzuci w zmienne ktre opisuj co te warunki znacz
        if (lesson.week && lesson.week !== currentWeek) {
          return false;
        }
        if (lesson.type === "all") {
          return true;
        }
        return lesson.group === settings[lesson.type];
      })
      .map(({ time, start, end, type, group, week, ...rest }) => {
        const isTimeSet = time !== undefined;
        const newStart = isTimeSet ? hours[time].start : start;
        const newEnd = isTimeSet ? hours[time].end : end;
        return { ...rest, start: newStart, end: newEnd };
      })
  );
  return filteredPlan;
};

const getNextLessonsDay = plan => {
  const now = new Date();
  const day = (now.getDay() + 7) % 8;
  if (day >= 5) {
    return 0;
  }
  const isAfterLessons = day =>
    now.getHours() * 60 + now.getMinutes() >
    plan[day][plan[day].length - 1].end;

  return R.pipe(
    R.when(isAfterLessons, R.inc),
    R.when(day => day >= 5, R.always(0))
  )(day);
};

export class App extends Component {
  constructor() {
    super();

    const defaultSettings = {
      base_group: "1",
      german_group: "2",
      rel_eth_group: "eth",
      ckp_group: "3"
    };
    const settings = storage.getData() || defaultSettings;

    const filteredPlan = filterAndMapPlan(plan, settings);

    const day = getNextLessonsDay(filteredPlan);

    this.state = {
      selectedDay: day,
      userSettings: settings,
      plan: filteredPlan
    };
  }
  handleSettingsChange = settings => {
    this.setState(
      prevState => {
        const newSettings = { ...prevState.userSettings, ...settings };
        const filteredPlan = filterAndMapPlan(plan, newSettings);
        return {
          userSettings: newSettings,
          plan: filteredPlan
        };
      },
      () => {
        storage.setData(this.state);
      }
    );
  };

  handleDayChange = (event, data) => {
    this.setState(() => ({
      selectedDay: data["data-value"]
    }));
  };

  render() {
    return (
      <div>
        <Header onDayChange={this.handleDayChange} />

        {this.state.selectedDay === -1 ? (
          <Settings onSettingsChange={this.handleSettingsChange} />
        ) : (
          <Day
            lessons={this.state.plan[this.state.selectedDay]}
            day={this.state.selectedDay}
          />
        )}
      </div>
    );
  }
}



