import React, { Component } from "react";

import { Button } from "./Button";

export class Settings extends Component {
  handleChange = (event, data) => {
    this.props.onSettingsChange({
      [data['data-group']]: data['data-value'],
    });
  }
  render() {
    return (
      <div className="settings">
        <div className="settings__group">
          etyka/religia:
          <Button data-group="rel_eth_group" data-value="nope" onClick={this.handleChange}>Nope</Button>
          <Button data-group="rel_eth_group" data-value="eth" onClick={this.handleChange}>Etyka</Button>
          <Button data-group="rel_eth_group" data-value="rel" onClick={this.handleChange}>
            Religia
          </Button>
        </div>
        <div className="settings__group">
          grupa - angielski:
          <Button data-group="base_group" data-value="1" onClick={this.handleChange}>1</Button>
          <Button data-group="base_group" data-value="2" onClick={this.handleChange}>2</Button>
        </div>
        <div className="settings__group">
          grupa - niemiecki:
          <Button data-group="german_group" data-value="1" onClick={this.handleChange}>1</Button>
          <Button data-group="german_group" data-value="2" onClick={this.handleChange}>2</Button>
        </div>
        <div className="settings__group">
          grupa - ckp:
          <Button data-group="ckp_group" data-value="1" onClick={this.handleChange}>1</Button>
          <Button data-group="ckp_group" data-value="2" onClick={this.handleChange}>2</Button>
          <Button data-group="ckp_group" data-value="3" onClick={this.handleChange}>3</Button>
        </div>
      </div>
    );
  }
}



