import React, { Component } from "react";
import { Button } from "./Button";

const options = [
  { name: "Pon", value: 0 },
  { name: "Wt", value: 1 },
  { name: "r", value: 2 },
  { name: "Czw", value: 3 },
  { name: "Pt", value: 4 },
  { name: "@", value: -1 }
];

export class Header extends Component {
  state = {};
  render() {
    return (
      <header className="header">
        {options.map(option => (
          <Button
            key={option.value}
            data-value={option.value}
            onClick={this.props.onDayChange}
          >
            {option.name}
          </Button>
        ))}
      </header>
    );
  }
}



